@charset "UTF-8";
@import url(../../node_modules/bootstrap/dist/css/bootstrap.min.css);
@media (min-width: 576px) { .menu { display: none; }
  .menu + div.phone-number { display: none; }
  .eset.logo { display: none; } }

@media (min-width: 1200px) { .menu { display: inline-block; margin-right: 30px; }
  li.menu-item { font-size: 15px; margin-right: auto; margin-left: auto; } }

@media (min-width: 1514px) { li.menu-item { font-size: 18px; } }

@media (min-width: 1544px) { .menu + div.phone-number { display: inline-block; margin-right: 0px; margin-left: auto; }
  .phone-number + .submit { margin-left: 35px; margin-right: 0px; } }

@media (min-width: 1665px) { .eset.logo { display: inline-block; }
  .sdd.logo { border-right: 1px solid #ced7f1; } }

@font-face { font-family: 'BeauSans Bold'; src: url("/../fonts/PFBeauSansPro-Bold.eot"); src: local("PFBeauSansPro-Bold"), url("/../fonts/PFBeauSansPro-Bold.eot?#iefix") format("embedded-opentype"), url("/../fonts/PFBeauSansPro-Bold.woff") format("woff"), url("/../fonts/PFBeauSansPro-Bold.ttf") format("truetype"); font-weight: bold; font-style: normal; }

@font-face { font-family: 'BeauSans Thin'; src: url("/../fonts/PFBeauSansPro-Thin.eot"); src: local("PFBeauSansPro-Thin"), url("/../fonts/PFBeauSansPro-Thin.eot?#iefix") format("embedded-opentype"), url("/../fonts/PFBeauSansPro-Thin.woff") format("woff"), url("/../fonts/PFBeauSansPro-Thin.ttf") format("truetype"); font-style: normal; }

@font-face { font-family: 'BeauSans Regular'; src: url("/../fonts/PFBeauSansPro-Regular.eot"); src: local("PFBeauSansPro-Regular"), url("/../fonts/PFBeauSansPro-Regular.eot?#iefix") format("embedded-opentype"), url("/../fonts/PFBeauSansPro-Regular.woff") format("woff"), url("/../fonts/PFBeauSansPro-Regular.ttf") format("truetype"); font-style: normal; }

.brdr { border: 1px solid black; }

body { background: #272727; height: auto; margin: 0; padding: 0; align-items: center; flex-direction: column; justify-content: flex-start; text-align: center; font-family: 'BeauSans Regular'; font-style: normal; }

header { position: fixed; z-index: 100; display: flex; flex-direction: row; align-items: center; width: 100%; padding: 12px; margin-right: 0; padding-left: 0; background-color: white; box-shadow: rgba(50, 50, 50, 0.1) 0px 2px 10px 1px; transition: ease-in-out box-shadow 0.25s; }

header:hover { box-shadow: rgba(50, 50, 50, 0.24) 0px 3px 26px 2px; }

.sdd.logo { width: auto; height: auto; padding-right: 20px; padding-left: 15px; }

.eset.logo { padding-right: 30px; padding-left: 20px; margin-right: 0; }

.menu { height: 100%; vertical-align: center; margin-right: auto; display: none; }

.menu > li { height: 100%; vertical-align: center; display: inline-block; list-style-type: none; padding: 16px; font-family: 'BeauSans Regular'; font-style: normal; font-weight: 500; margin-right: auto; }

.menu > li:hover { color: #295eaf; text-shadow: 0px 0px 2px #335ba54b; }

.submit { margin-left: auto; margin-right: 0; border: none; padding-left: 22px; padding-right: 22px; font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; color: white; background-color: #398ae6; text-transform: uppercase; transition: ease-in 0.15s; }

.submit:hover { background-color: #1864bb; transition: ease-out 0.15s; }

.cart { margin-left: 23px; margin-right: 20px; width: 27px; height: 27px; }

.phone-number { display: inline-block; margin-left: auto; margin-right: 5px; font-size: 18px; font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; color: #2e2e2e; }

.products-section { display: flex; flex-direction: column; width: 100%; background-color: #eaf2f4; padding-top: 20px; padding-bottom: 80px; }

.products-cards { display: flex; align-items: center; justify-content: center; flex-direction: column; }

.product-2 { border: 2px solid #398ae6; }

.product { display: flex; flex-direction: column; justify-content: flex-end; margin: 10px 15px 50px; width: 90%; height: 580px; background-color: #f7f7f7; border-radius: 8px; box-shadow: rgba(50, 50, 50, 0.23) 0px 3px 12px 1px; transition: ease-in transform 0.20s, margin 0.32s, box-shadow 0.25s; }

.product:hover { background-color: white; transform: scale(1.022, 1.022) translate(0, -20px); box-shadow: rgba(50, 50, 50, 0.2) 0px 12px 30px 2px; position: relative; transition: ease-out transform 0.18s, margin 0.29s, box-shadow 0.20s; }

.products-title { font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; margin-top: 130px; font-size: 40px; margin-bottom: 23px; }

hr { width: 75px; height: 4px; margin-bottom: 60px; border: #017fd4 1px solid; border-radius: 15px; background-color: #017fd4; }

.product-price { font-size: 32px; margin-top: 40px; margin-bottom: -10px; font-family: 'BeauSans Thin'; font-weight: 150; font-style: thin; }

.product-price:after { content: ""; font-size: 18px; padding-left: 4px; }

.product > .product-card-title { font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; width: 150px; word-spacing: 20px; font-weight: bold; font-size: 18px; text-align: center; margin-top: 30px; }

.product-description { max-width: 75%; display: block; font-family: 'BeauSans Regular'; font-style: normal; font-size: 15px; }

.product > * { width: 80%; display: block; margin: 10px auto; }

.product .buttons { width: 80%; height: 115px; display: flex; flex-direction: column; align-content: center; justify-content: space-between; margin-top: 35px; margin-bottom: 40px; }

input[type="button"] { height: 50px; border-radius: 4px; outline: none; font-size: 16px; }

input[type="button"]:active { border-radius: 6px; outline: none; }

.buy { color: white; background: #228be0; border: #228be0 1px solid; font-weight: bold; text-transform: uppercase; font-size: 12px; transition: ease-out 0.14s; }

.buy:active { color: white; border: #079be6 1px solid; font-weight: bold; background-color: #00314a; }

.buy:hover { background-color: #0175b0; }

.learn-more { background: white; border: navy 2px solid; color: #092f68; transition: ease-out 0.14s; font-weight: bold; font-family: 'BeauSans Regular'; font-style: normal; text-transform: uppercase; font-size: 12px; }

.learn-more:active { background: #dedede; border: #142b57 2px solid; color: #163264; font-weight: bold; }

.learn-more:hover { background: #142b57; border: #142b57 2px solid; color: white; }

.product .product-sticker { height: 35px; margin-top: -17px; padding: 8px 16px 1px 16px; border-radius: 40px; color: white; font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; text-transform: uppercase; font-size: 14px; }

.sticker-container { display: flex; flex-direction: row; justify-content: center; align-content: center; position: relative; width: 100%; height: 1px; margin-top: 0; margin-bottom: auto; }

.special { height: 25px; margin-top: -17px; padding: 8px 16px 1px 16px; border-radius: 40px; color: white; font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; text-transform: uppercase; font-size: 14px; background-color: #398ae6; margin-top: -22px; margin-bottom: -25px; }

.new { height: 25px; margin-top: -17px; padding: 8px 16px 1px 16px; border-radius: 40px; color: white; font-family: 'BeauSans Bold'; font-weight: bold; font-style: normal; text-transform: uppercase; font-size: 14px; background-color: #9fca4e; margin-top: -26px; }

img[type="svg"] { margin-top: 10px; margin-bottom: -12px; }

footer { display: flex; height: 250px; justify-content: center; align-items: center; color: azure; }

@media (min-width: 576px) { .products-cards .product { width: 510px; }
  .menu { display: none; } }

@media (min-width: 768px) { .products-cards { flex-direction: row; flex-wrap: wrap; }
  .menu-mobile { display: block; }
  .products-cards .product { width: 328px; }
  .product > .product-card-title { width: 150px; word-spacing: 20px; }
  .product-card-title { width: 220px; font-size: 19px; }
  .product-description { width: 300px; font-size: 14px; }
  .product-price { font-size: 50px; }
  .product-price:after { font-size: 26px; } }

@media (min-width: 992px) { .products-cards .product { width: 287px; }
  .product-description { width: 180px; font-size: 18px; }
  .product > .product-card-title { width: 180px; font-size: 22px; } }

@media (min-width: 1200px) { .products-cards .product { width: 350px; flex-wrap: nowrap; }
  .menu { display: block; }
  .product-card-title { width: 200px; }
  .product-description { width: 250px; } }

@media (min-width: 1514px) { .products-cards .product { width: 450px; flex-wrap: nowrap; }
  .eset.logo { display: inline-block; } }

.eset.logo { display: none; }

.phone-number { display: none; }

.red-color { color: red; }
